@media screen and (min-width: 1025px) and (max-width:1919px) {
    .navMenu {
        .navSictionsMenu {
            .navMenuSections {
                margin-right: 13px;
            }
        }
    }

}

@media screen and (min-width: 1280px) and (max-width:1599px),
(min-width: 1025px) and (max-width:1279px) {
    .navMenu {
        padding: 0 25px;

        .navSictionsMenu {
            height: 70px;

            .navMenuSections {
                font-size: 11px;

                .navMenuSectionsCategorys {

                    width: 220px;
                    top: 69.8px;

                    .categories {
                        font-size: 0;
                    }
                }

                .navMenuSectionsCategorys.active {
                    padding: 10px 25px;

                    .categories {
                        font-size: 10px;

                    }
                }
            }
        }

    }
}


@media screen and (max-width: 1120px) {
    .navMenu {
        position: fixed;
        width: auto;
        background-color: #ebebeb;
        padding: 0;
        align-items: flex-start;
        height: 100%;
        overflow: scroll;
        display: flex;
        top: 0;
        left: 0;
        border-top: 0;
        transition: .5s;
        z-index: 200;

        .burgerNone {
            width: 200px;
        }


        .navSictionsMenu {
            padding: 30px;
            width: 100%;
            height: auto;
            width: auto;
            flex-direction: column;
            gap: 20px;

            .navMenuSections {
                display: flex;
                width: 100%;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                // font-size: 11px;
                margin-right: 0px;
                font-weight: 500;
                position: relative;
                cursor: pointer;
                transition: all 0.5s ease;

                .navMenuSectionsLink {
                    text-align: center;

                    .downIcon {
                        margin-left: 0;

                    }


                }

                .navMenuSectionsCategorys {
                    display: flex;
                    background: rgb(246, 246, 246);
                    position: static;
                    flex-direction: column;
                    text-align: left;
                    width: max-content;
                    top: 47px;
                    transition: all 0.5s ease;
                    font-size: 0;
                    border: 0;
                    width: 100%;
                    height: 0;
                    padding: 0;
                    overflow: hidden;
                    white-space: nowrap;
                    cursor: default;

                    .categories {
                        white-space: nowrap;
                        display: flex;
                        justify-content: center;
                        cursor: pointer;
                        // font-size: 10px;
                        margin: 3px 0;
                        color: black;
                    }

                    .categories:hover {
                        transition: all 0.5s ease;
                        color: grey;
                        transform: scale(1.05);
                    }
                }

                .navMenuSectionsCategorys.active {
                    font-size: 15px;
                    border: 1px solid grey;
                    height: auto;
                    padding: 10px 25px;
                    cursor: default;
                    width: 100%;

                }
            }

            .navMenuSections:hover {
                color: grey;
            }

            .sectionCategory {
                cursor: pointer;
            }

        }

    }

    .navMenu.active {
        left: -200%;
    }
}

