.registrationAndLoginField {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    z-index: 300;

    .overlay {
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.604);
        z-index: 310;
        display: flex;
        justify-content: center;
        align-items: center;
        .loginAndRegistrationForm {
            width: 20%;
            position: fixed;

            .cloaseModal {
                padding: 10px 10px 0 0;
                background-color: #ebebeb;
                display: flex;
                font-size: 20px;
                justify-content: flex-end;

                .cloaseModalIcon {
                    cursor: pointer;
                }
            }

            h2 {
                padding: 10px;
                padding-top: 5px;
                text-align: center;
                font-size: 16px;
                background-color: #ebebeb;
            }

            .form {
                height: auto;
                width: 100%;
                background: white;
                display: flex;
                flex-direction: column;
                padding: 0 20px 20px 20px;

                label {
                    margin-top: 20px;
                    font-size: 12px;
                    color: gray;
                    border-radius: 50px;

                }

                input[type=password],
                input[type=text],
                input[type=email],
                input[type=tel] {
                    margin-top: 3px;
                    border-radius: 5px;
                    border: 1px solid gray;
                    padding: 5px 5px;
                }

                input[type=checkbox] {
                    margin-top: 1.5px;
                    margin-right: 10px;

                }


                label:last-of-type {
                    display: flex;
                    align-items: flex-start;
                }



                .onSubmitButton {
                    margin-top: 20px;
                    display: flex;
                    justify-content: center;

                    button {
                        p {
                            width: 100%;
                            text-align: center;
                        }
                    }

                }

                .registrationOnLoginPage {
                    margin-top: 15px;
                    width: 100%;
                    color: black;
                }

                .registrationOnLoginPage:hover {
                    color: grey;
                }

                .error {
                    font-size: 9px;
                    color: red;
                }

            }
        }

    }
}