.shopAdvertisement {
    width: 100%;
    height: 100%;
    position: relative;

    .shopAdvertisementImg {
        width: 100%;
        aspect-ratio: 16/9;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .overlayShopAdvertisement {
        color: white;
        padding:0 10% ;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 100;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;
        gap: 20px;
        background: rgba(0, 0, 0, 0.604);

        .textTop{
            font-size: 55px;
        }
        .textBottom{
            font-size: 44px;
        }
    }
}