.scrollTopButton {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid grey;
    background: rgba(0, 0, 0, 0.238);
    border-radius: 6px;
    cursor: pointer;
    .scrollTopButtonIcon{
        color: white;
    }
}