.filtersBar {
    transition: .5s;
    width: 342px;
    height: 100%;
    background: transparent;
    position: fixed;
    top: 0;
    right: -100%;
    z-index: 120;
    display: flex;
}

.filtersBar.active {
    right: 0;
    transition: .5s;
}

.sortAndFilterButtons {
    margin-top: 30px;
    display: flex;
    width: 100%;
    position: relative;
    align-items: center;
    justify-content:space-between;
    padding: 0 20px;
}

.filterButton {
    font-size: 11px;
    padding: 10px 25px;
    border: 1px solid grey;
    justify-content: flex-end;
    cursor: pointer;
    display: flex;

    .filterIcon {
        display: flex;
        align-items: center;
        padding-left: 5px;
        font-size: 14px;
    }
}

.productFilters {
    background-color: #ebebeb;
    overflow: scroll;
    padding: 20px;
    gap: 50px;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: 200;

    .productPriceFilter {
        position: relative;


        .tooltipPrice {
            display: flex;
            width: 100%;
            color: grey;
            justify-content: space-between;
        }

        .togglePriceInputs {
            display: flex;
            margin-top: 10px;
            gap: 50px;

            input[type="text"] {
                border: none;
                border-radius: 6px;
                color: rgb(31, 31, 31);
                width: 100%;
                height: auto;
                cursor: pointer;
                outline: none;

            }

            input[type="text"]:placeholder {
                color: grey;
            }
        }
    }

    .subCategoriesToggleList {
        .subCategoriesList {
            .subCategories {
                .subCategorieInputs {
                    position: relative;
                    display: inline-block;
                    cursor: pointer;
                    display: flex;
                    align-items: center;
                    font-size: 16px;
                    line-height: 1.5;

                    input[type="checkbox"] {
                        opacity: 0;
                        margin-right: 10px;
                        cursor: pointer;
                    }

                    .checkmark {
                        position: absolute;
                        top: 5px;
                        margin-right: 10px;
                        left: 0;
                        height: 15px;
                        width: 15px;
                        background-color: grey;
                        border: 7.5px solid #ffffff;
                    }

                    input[type="checkbox"]:checked~.checkmark {
                        display: block;
                        height: 15px;
                        width: 15px;
                        background: grey;
                        border: none;
                        clip-path: polygon(20% 0%, 0% 20%, 30% 50%, 0% 80%, 20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%);
                    }
                }
            }
        }
    }
}

