.mobileTabBar {
    bottom: 0;
    width: 200px;
    left: 0;
    z-index: 80;
    padding: 10px 20px;

    .tabBarCategories {
        display: flex;
        justify-content: space-between;

        .tabBarCategory.active {
            color: black;

            a {
                color: black;
            }
        }

        .tabBarCategory {
            color: grey;
            font-size: 25px;
            cursor: pointer;

            a {
                height: auto;
                color: grey;
            }

        }

        .tabBarCategory:nth-of-type(1),
        .tabBarCategory:nth-of-type(2) {
            display: none;
        }

    }
}