.sortCategories {
    height: 37px;
    display: flex;
    white-space: nowrap;
    font-size: 11px;
    width: 287px;
    padding: 10px 25px;
    padding-right: 0;
    border: 1px solid grey;


    .selectedSortCategory {
        cursor: pointer;
        color: black;
        width: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
    }

    .selectedSort {
        margin-right: 25px;
        cursor: pointer;
    }

    .sortList {
        top: 25px;
        right: -1px;
        transition: height 0.5s ease, padding 0.5s ease;
        position: absolute;
        height: 0;
        border: 1px solid grey;
        border-top: 0;
        padding-top: 0;
        flex-direction: column;
        padding: 0;
        display: flex;
        gap: 10px;
        width: 150px;
        z-index: 80;
        background: white;

        .sortButton {
            height: auto;
            font-size: 0;
            opacity: 0;
        }
        .sortButton.active {
          color: grey;
        }
        .sortButton:hover {
            color: grey;
        }
    }

}

.sortCategories:hover {
    .sortList {
        padding: 20px;
        height: 160px;

        .sortButton {
            transition: opacity 0.5s ease .2s;
            opacity: 1;
            font-size: 11px;
        }
    }
}