@media screen and (max-width:1120px) {
    .languageButton {
        width: auto;
    }
}

@media screen and (max-width:499px) and (max-height:1000px) {
    .languageButton {
        width: 30px;
        height: 30px;

        .languageButtonIcon {
            width:100%;
            height: 100%;

        }
    }
}

@media screen and (max-width:320px) and (max-height:1000px) {
    .languageButton {
        width: 25px;
        height: 25px;
        .languageButtonIcon {
            width:100%;
            height: 100%;

        }
    }
}