.search {
    border-radius: 10px;
    height: 45px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: white;
    align-items: center;
    transition: .5s;
    z-index: 100;
    padding-bottom: 60px;

    form {
        transition: .5s;
        width: 100%;

        height: auto;
        height: auto;
        padding:20px 5px;
        background: #cbcbcb;
        .searchInput {
            padding:0 10px;
            height: 40px;
            border-radius: 10px;
            width: 100%;
            border: none;
        }

        .searchInput::-webkit-search-cancel-button{
            appearance: none;
            display: inline-block;
            width: 15px;
            height: 15px;
            background: #000;
            background: url('../../resources/img/X.png') no-repeat center center /cover;
            cursor: pointer;
        }
        .searchInput::placeholder {
            color: grey;
      
            font-size: 16px;
        }

        .searchInput:focus {
            outline: none;
        }

        .searchInput.active {
            width: 100%;
        }

    }

    .searchIcon {
        font-size: 16px;
        cursor: pointer;
        transition: .5s;
    }

    .searchList {
        background: white;
        width: 100%;
        height: 100%;

        .searchInfo {
            .searchResult {
                border-top:1px solid rgb(0, 0, 0) ;
                color: grey;
                display: flex;
                justify-content: space-between;
                background: white;
                font-size: 12px;
                padding: 20px;

                div {
                    display: flex;
                    gap: 20px;
                }
            }

            .searchWindowCloaseIcon {
                cursor: pointer;
            }

            .searchProducts {
                width: 100%;
                padding: 10px 20px;
                background: #ebebeb;
                color: grey;
                font-size: 12px;
            }
        }


        .foundProducts {
            display: flex;
            flex-wrap: wrap;
            overflow: scroll;
            width: 100%;
            max-height: 70%;
            padding: 20px;
            gap: 20px;
        }
    }
}
