@media screen and (min-width: 1280px) and (max-width:1599px){
    .button {
        font-size: 11px;
        padding: 10px 25px;
    }
    .titleSection {
        font-size: 45px;
        margin: 20px 0;
    }
}


@media screen and (min-width: 1024px) and (max-width:1279px){
    .button {
        font-size: 9px;
        padding: 7px 20px;
    }
    .titleSection {
        font-size: 35px;
        margin: 20px 0;
    }
}

@media screen and (min-width: 720px) and (max-width:1023px){
    .button {
        font-size: 11px;
        padding: 10px 25px;
    }
    .titleSection {
        font-size: 25px;
        margin: 20px 0;
    }
}


@media screen and (min-width: 500px) and (max-width:719px) and (max-height:1000px) {
    .button {
        font-size: 11px;
        padding: 5px 15px;
    }
    .titleSection {
        font-size: 19px;
        margin: 20px 0;
    }
}

@media screen and (max-width:499px) and (max-height:1000px) {
    .button {
        font-size: 9px;
        padding: 5px 15px;
    }
    .titleSection {
        font-size: 17px;
        margin: 10px 0;
    }
}