.sliderSection {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;

    .newsAndSaleSliderParam {
        width: 100%;
        position: relative;

        .custom-arrow.custom-prev,
        .custom-arrow.custom-next {
            position: absolute;
            width: 100px;
            height: 100%;
            top:0;
            display: flex;
            justify-content: center;
            align-items:center;
            z-index: 100;
        }
        .custom-arrow.custom-prev{
            left: 0px;
            font-size: 50px;
        }
        .custom-arrow.custom-next {
            right: 0px;
            font-size: 50px;
        }

        .slick-list {
            height: 100%;

            .slick-track {
                height: 100%;


                .slick-slide {
                    height: 100%;
                }
            }
        }

      
        .slick-dots {
            bottom: -40px;
            left: 0px;

            li {
                margin: 0 20px;

                button:before {
                    font-size: 15px;
                }
            }
        }
    }
}