.navMenu {
    position: sticky;
    top: 0px;
    display: flex;
    padding: 0 50px;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    align-items: center;
    justify-content: space-between;
    background-color: #ebebeb;
    z-index: 100;
    white-space: nowrap;

    .navSictionsMenu {
        display: flex;
        height: 80px;

        .navMenuSections {
            display: flex;
            align-items: center;
            margin-right: 16px;
            font-weight: 400;
            font-size: 16px;
            cursor: pointer;
            transition: all 0.5s ease;

            .navMenuSectionsLink {
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                text-decoration: none;
                text-align: start;
                color: black;

                .downIcon {
                    margin-left: 5px;
                }

                .downIcon.active {
                    transform: rotate(180deg);
                }

            }

            .navMenuSectionsLink.active {
                color: grey;
            }
            .navMenuSectionsLink:hover{
                color: grey;
            }


            .navMenuSectionsLink.active {
                z-index: 100;
            }

            .navMenuSectionsCategorys {
                display: flex;
                background: #ebebeb;
                position: absolute;
                flex-direction: column;
                width:311px;
                top: 79.2px;
                gap: 5px;
                // transition: all 0.3s ease;
                font-size: 0;
                border: 1px solid black;
                border-top: none;
                border-bottom: none;
                height: 0;
                padding: 0;
                overflow: hidden;
                cursor: default;

                .categories {
                    cursor: pointer;
                    padding: 5px 0;
                    color: black;

                    .categoriesLink {
                        color: black;
                    }
                    .categoriesLink.active {
                        color: grey;
                    }
                    .categoriesLink:hover {
                        color: grey;
                    }
                }
            }

            .navMenuSectionsCategorys.active {
                font-size: 15px;
                transition: all 0.3s ease;
                border-bottom:1px solid black;
                height: auto;
                padding: 20px 35px;
                cursor: default;

            }

        }

        .navMenuSections:hover {
            color: grey;
        }

        .sectionCategory {
            cursor: pointer;
        }

    }

}