@media screen and (max-width:720px) {
    .sortCategories {
        height: 28px;
        width: 245px;
        padding: 5px 15px;
        padding-right: 0;

        .selectedSort {
            margin-right: 10px;
        }

        .sortList {
            top: 21px;
            width: 133px;
        }
    }

    .sortCategories:hover {
        .sortList {
            padding: 10px;
            height: 137px;
        }
    }
}

@media screen and (max-width:425px) {
    .sortCategories {
        height: 27px;
        width: 133px;
        padding:5px 10px;
        padding-right: 0;

        .selectedSort {
            margin-right: 7px;
        }

        .sortList {
            top: 20px;
        }
    }


}