.errorPages {
    position: relative;

    .errorPagesImg {
        width: 100%;
        height: 100%;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .errorPagesTextBlok {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgba(0, 0, 0, 0.604);

        .errorPagesText {
            font-size: 50px;
            font-weight: bolder;
            color: white;
        }
    }
}