@media screen and (min-width: 1024px) and (max-width:1599px) {
    .sliderSection {
        .newsAndSaleSliderParam {

            .custom-arrow.custom-prev,
            .custom-arrow.custom-next {
                font-size: 40px;
            }

            .slick-dots {
                li {
                    margin: 0 20px;

                    button:before {
                        font-size: 11px;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 720px) and (max-width:1023px) {
    .sliderSection {
        .newsAndSaleSliderParam {

            .custom-arrow.custom-prev,
            .custom-arrow.custom-next {
                font-size: 30px;
            }

            .slick-dots {
                li {
                    margin: 0 10px;

                    button:before {
                        font-size: 10px;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 500px) and (max-width:719px) and (max-height:1000px),
(max-width:499px) and (max-height:1000px) {
    .sliderSection {
        .newsAndSaleSliderParam {

            .custom-arrow.custom-prev,
            .custom-arrow.custom-next {
                font-size: 25px;
                width: 50px;
            }

            .slick-dots {

                li {
                    margin: 0 5px;
                    width: 10px;
                    height: 10px;

                    button:before {
                        font-size: 7px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width:499px) and (max-height:1000px) {
    .sliderSection {
        .newsAndSaleSliderParam {

            .custom-arrow.custom-prev,
            .custom-arrow.custom-next {
                font-size: 20px;
                width: 25px;
            }


            .slick-dots {
                li {
                    width: 20px;
                    height: 20px;
                    margin: 0 2px;

                    button {
                        padding: 0;

                    }

                    button:before {
                        font-size: 7px;
                    }
                }
            }
        }
    }
}