@media screen and (max-width:1120px) {
    .mobileTabBar {
        background: #ebebeb;
        position: fixed;
        width: 100%;
        z-index: 300;

        .tabBarCategories {

            .tabBarCategory:nth-of-type(1),
            .tabBarCategory:nth-of-type(2) {
                display: inline-block;
            }
        }
    }
}


@media screen and (max-width:499px) and (max-height:1000px) {
    .mobileTabBar {
        .tabBarCategories {
            .tabBarCategory {
                font-size: 20px;    
            }
        }
    }
}

@media screen and (max-width:320px) and (max-height:1000px) {
    .mobileTabBar {
        .tabBarCategories {
            .tabBarCategory {
                font-size: 15px;    
            }
        }
    }
}