.basket {
    min-height: 500px;
    width: 100%;

    .basketList {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        gap: 20px;

        .basketProducts {
            width: 100%;

            .basketProduct {
                width: 100%;
                padding: 0 20px;
                display: flex;
                // justify-content: space-between;
                align-items: center;
                border-bottom: 1px solid black;
                height: 100px;

                .removeProdFromBasket {
                    background: rgb(255, 255, 255);
                    position: relative;
                    width: 20px;
                    height: 30px;
                    margin-right: 20px;
                    cursor: pointer;
                }

                .removeProdFromBasket::before {
                    content: '';
                    clip-path: polygon(0 45%, 100% 45%, 100% 55%, 0 55%);
                    width: 20px;
                    height: 30px;
                    top: 0px;
                    left: 0px;
                    position: absolute;
                    background: #a80000;

                }


                .imgBasketProd {
                    margin-right: 20px;
                    height: 98px;
                    width: 98px;
                }

                .nameProduct {
                    margin-right: 20px;
                    max-width: 300px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis
                }

                .quantityProducts {
                    width: 100px;
                    margin-right: 20px;
                    display: flex;
                    justify-content: center;
                    gap: 10px;
                    align-items: center;
                    height: 100%;

                    .incQuantityProd {
                        width: 13px;
                        height: 13px;
                        position: relative
                    }

                    .incQuantityProd::before {
                        content: '';
                        clip-path: polygon(0 45%, 45% 45%, 45% 0, 55% 0, 55% 45%, 100% 45%, 100% 55%, 55% 55%, 55% 100%, 45% 100%, 45% 55%, 0 55%);
                        width: 13px;
                        height: 13px;
                        top: 0px;
                        left: 0px;
                        position: absolute;
                        background: #000000;

                    }

                    .decQuantityProd {
                        width: 13px;
                        height: 13px;
                        position: relative;

                    }

                    .decQuantityProd::before {
                        content: '';
                        clip-path: polygon(0 45%, 100% 45%, 100% 55%, 0 55%);
                        width: 13px;
                        height: 13px;
                        top: 0px;
                        left: 0px;
                        position: absolute;
                        background: #000000;

                    }
                }
            }
        }

        .orderAndDeleteButtone {
            display: flex;
            gap: 20px;
            .orderAllProducts,
            .deleteAllProductsForBasket {
                width:auto;
                background-color: grey;
                color: white;
                border: 1px solid grey;
                padding: 10px 25px;
            }

        }
    }
}